// Banner.js
// 메인 배너

import React from 'react';
import { FullpageSection } from '@ap.cx/react-fullpage'; // 풀 페이지 섹션
import {animated, useSpring} from 'react-spring';

import './Banner.css';


function Banner() {


  const opa = useSpring({
    from: {
      transition: 'all 10s ease-in-out',
      opacity: '0',
      backgroundColor: 'yellow',
    },

    to: {
      opacity: '1',
      backgroundColor: '#fff',


    },
  })




    const initStyle = {
        display: 'flex',
        justifyContent: 'center',
        alignItems: 'center',
        height: '100vh',
        backgroundColor: '#fff',
    }

  return (
    <div>
        <FullpageSection style={initStyle}>
          <animated.div className='wrap' style={opa}>
          <h1>메인 배너 페이지</h1>
            <p>메인 배너</p>
          </animated.div>
      
        </FullpageSection>
    </div>
  )
}

export default Banner;
